#popular-article, #latest-article, #opinion-article, #food-article {
  flex-flow: wrap;
  justify-content: center;
  display: flex;
}

#popular-article article {
  border-bottom: double var(--black);
  border-right: solid var(--black);
  align-content: center;
  width: 9rem;
  height: auto;
  margin: .5rem;
  padding: 1rem;
}

#popular-article a, #latest-article a {
  color: var(--black);
  font-family: Cabin, sans-serif;
  font-size: 1rem;
  font-weight: bold;
  text-decoration: none;
}

#latest-article article {
  border-bottom: double var(--black);
  border-right: solid var(--black);
  align-content: center;
  width: 21rem;
  height: auto;
  margin: .5rem;
  padding: 1rem;
  display: flex;
}

.img-div, .text-div {
  margin: .5rem;
}

#food-article article {
  border-bottom: 1px solid var(--black);
}

#food-article a {
  color: var(--black);
  font-family: Cabin, sans-serif;
  font-size: 1rem;
  font-weight: bold;
  text-decoration: none;
}

#food-article article .img-div img, #opinion-article article .img-div img {
  width: 100px;
}
